import React, { Component } from "react";
import { Form, Row, Col, Input, Button, Icon } from "antd";
import TextArea from "antd/lib/input/TextArea";
import { withRouter } from "react-router";

import "./index.less";

class index extends Component {
  componentDidMount() {
    const { match, location, history } = this.props;
  }
  componentDidUpdate(prevProps) {
    if (this.props.array.values !== prevProps.array.values) {
      this.initValues();
    }
  }
  initValues = () => {
    this.props.form.setFieldsValue(this.props.array.values);
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      this.props.filterResult(err, values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  gotoLast = () => {
    this.props.history.go(-1);
  };
  render() {
    const { array } = this.props;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 3 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 21 }
      }
    };
    const formItemArray = (
      <div>
        {(array.itemArray || []).map((item, k) => (
          <Form.Item
            {...formItemLayout}
            key={`form-item-${k}`}
            label={item.label}
          >
            {getFieldDecorator(item.name, {
              rules: [
                {
                  required: item.required,
                  message: item.message
                }
              ]
            })(
              item.type === "textarea" ? (
                <TextArea placeholder={item.placeholder} rows="10" />
              ) : (
                <Input placeholder={item.placeholder} />
              )
            )}
          </Form.Item>
        ))}
      </div>
    );
    return (
      <div className="form-container">
        <Form onSubmit={this.handleSearch}>
          {formItemArray}
          <Button className="cancel" onClick={this.gotoLast}>
            返回
          </Button>
          <Button
            className="confirm"
            type="primary"
            loading={array.confirmButtonLoading}
            htmlType="submit"
          >
            确认
          </Button>
        </Form>
      </div>
    );
  }
}
const WrappedIndex = Form.create()(index);

export default withRouter(WrappedIndex);
