import React, { Component } from "react";
import { Button, Input, message } from "antd";
import { get, post } from "util/fetch";
import { testWord } from "util/check";
import "clipboard";
import "./index.less";
const { TextArea } = Input;
export default class index extends Component {
  state = {
    collection_id: 0,
    collectionData: { name: "", note: "" },
    rawWords: ``,
    sqlSentence: ""
  };

  componentDidMount() {
    this.init();
  }
  getUrlVars = url => {
    var vars = {};
    var parts = url.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m, key, value) {
      vars[key] = value;
    });
    return vars;
  };
  init = () => {
    let query = this.props.location.search;
    let collection_id = this.getUrlVars(query)["collection_id"];
    this.state.collection_id = collection_id;

    this.fetchCollectionData(collection_id);

    // 初始化粘贴板
    let clipboard = new ClipboardJS("#clip");

    clipboard.on("success", function(e) {
      message.success("复制成功");
    });
  };

  fetchCollectionData = collection_id => {
    get("/api/english/collection/" + collection_id).then(res => {
      this.setState({
        collectionData: res.data[0]
      });
      log(21, res.data[0]);
    });
  };

  goToLast = () => {
    this.props.history.go(-1);
  };

  handleChange = e => {
    this.setState({
      rawWords: e.target.value
    });
    // console.log(e.target.value);
  };

  translateWords = () => {
    const { rawWords, collection_id } = this.state;

    let str = rawWords
      .trim()
      .split("\n")
      .join(" ");

    let array = str
      .toLowerCase()
      .split(" ")
      .filter(item => {
        const flag = testWord(item);
        if (item !== "" && flag) {
          return true;
        }
      });
    let wordsString = array.map(item => `"${item}"`).join(",");
    let sqlSentence = `INSERT INTO collection_word_relation(word_id,collection_id) SELECT t1.id,${collection_id} from regular_word t1 LEFT JOIN collection_word_relation t2 ON t1.id = t2.word_id WHERE t1.word in (${wordsString}) AND t2.word_id is null`;
    this.setState({
      sqlSentence
    });
    console.log(12, str, array, wordsString, sqlSentence);
  };
  render() {
    const { collectionData } = this.state;
    return (
      <div className="collection-batch-container">
        <div className="header">
          <span className="span">{`词库：${collectionData.name}  ${
            collectionData.note
          } `}</span>
          <Button type="primary" onClick={this.goToLast} className="btn-back">
            返回
          </Button>
        </div>
        <div className="content">
          <div className="head">
            <span>录入</span>
            <Button type="primary" onClick={this.translateWords}>
              提交
            </Button>{" "}
          </div>
          <p>说明：请输入从网上复制的单词，允许包括中文</p>
          <TextArea
            onChange={this.handleChange}
            value={this.state.rawWords}
            name="rawWords"
            cols="30"
            rows="10"
          />
          <div className="head">
            <span>SQL表达式</span>
            <Button id="clip" type="primary" data-clipboard-target="#bar">
              复制
            </Button>{" "}
          </div>
          <p>说明：复制生成的sql语句，到数据库中运行</p>
          <TextArea
            value={this.state.sqlSentence}
            name="sqlSentence"
            id="bar"
            cols="30"
            rows="10"
          />
        </div>
      </div>
    );
  }
}
