import React, { Component } from "react";

import "./index.less";
import { itemArray } from "./data";
import Form from "co/form";
import { get, post, put } from "util/fetch";
import { message, Input } from "antd";
const Search = Input.Search;

const routePath = "/api/english/regular";
export default class index extends Component {
  state = {
    minHeight: window.innerHeight - 115,
    confirmButtonLoading: false,
    type: "create", // create and edit
    simpleId: "",
    data: {},
    quickData: {}
  };
  componentDidMount() {
    this.init();
  }

  init = () => {
    let pathname = this.props.location.pathname;
    let pathnameList = pathname.split("/");
    let type = pathnameList[pathnameList.length - 1];

    let query = this.props.location.search;
    query = query.split("?");
    let simpleId = query[1];
    this.setState({
      simpleId,
      type
    });
    if (type === "edit") {
      this.getWordInfo(simpleId);
    }
  };
  getWordInfo = simpleId => {
    const url = `${routePath}/${simpleId}`;
    get(url)
      .then(res => {
        let data = res.data[0] === undefined ? {} : res.data[0];

        this.setState({
          confirmButtonLoading: false,
          data
        });
        message.success("查询成功");
      })
      .catch(err => {
        this.setState({
          confirmButtonLoading: false
        });
        message.warn(err.errMsg);
      });
  };
  filterResult = (err, values) => {
    if (err) {
      return;
    }
    const { type, simpleId } = this.state;
    if (type === "edit") {
      this.updateWord(simpleId, values);
    } else if (type === "create") {
      this.createWord(values);
    }
  };
  updateWord = (simpleId, values) => {
    this.setState({
      confirmButtonLoading: true
    });
    put(`${routePath}/${simpleId}`, values)
      .then(res => {
        this.setState({
          confirmButtonLoading: false
        });
        message.success("更改成功");
      })
      .catch(err => {
        this.setState({
          confirmButtonLoading: false
        });
        message.warn(err.errMsg);
      });
  };
  createWord = values => {
    this.setState({
      confirmButtonLoading: true
    });
    post(`${routePath}`, values)
      .then(res => {
        this.props.history.go(-1);
        message.success("新增成功");
      })
      .catch(err => {
        this.setState({
          confirmButtonLoading: false
        });
        message.warn(err.errMsg);
      });
  };

  handleQuickSearch = value => {
    this.fetchExternWord(value);
  };
  fetchExternWord = word => {
    let url = `/api/common/video?word=${word}`;
    get(url)
      .then(res => {
        let data = res.data;

        let quickData = {
          word: word,
          definition_2: data.chineseInterpretation,
          sample_sentence_2: data.exampleSat,
          etyma: data.etyma,
          word_group: data.wordGroup,
          morphology: data.morphology,
          english_definition: data.englishInterpretation,
          video_path: data.aliVideoUrl
        };
        this.setState({
          data: Object.assign({}, this.state.data, quickData)
        });
        message.success("编辑成功");
        log(url, data);
      })
      .catch(err => {
        log(url, err);
      });
  };
  render() {
    const { minHeight, simpleId, data, confirmButtonLoading } = this.state;
    const array = { itemArray, confirmButtonLoading, values: data };
    return (
      <div className="regular-edit-container" style={{ minHeight }}>
        <div className="title">
          <div className="icon" />
          单词编辑
        </div>
        <div className="quick-search">
          <span>快捷录入:</span>
          <Search
            placeholder="input search text"
            enterButton="Search"
            style={{ width: 300 }}
            onSearch={value => this.handleQuickSearch(value)}
          />
        </div>
        <Form array={array} filterResult={this.filterResult} />
      </div>
    );
  }
}
