import React, { Component } from "react";
import { setValue } from "@/redux/simple-word";
import { connect } from "react-redux";
import {
  Form,
  Row,
  Col,
  Input,
  Button,
  Icon,
  message,
  Divider,
  Tag,
  Tooltip,
  Pagination
} from "antd";
import Filter from "co/filter";
import Table from "co/table";
import { get, post } from "util/fetch";
import { filters, columns } from "./data";

import "./index.less";

const routePath = "/api/english/regular";
const routePagePath = "/regular-english";
class index extends Component {
  state = {
    searchButtonLoading: false,
    source: [],
    pagination: {},
    filterResult: {},
    collectionData: { name: "", note: "" }
  };

  componentDidMount() {
    this.init();
  }
  getUrlVars = url => {
    var vars = {};
    var parts = url.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m, key, value) {
      vars[key] = value;
    });
    return vars;
  };
  init = () => {
    let pathname = this.props.location.pathname;
    let pathnameList = pathname.split("/");
    let type = pathnameList[pathnameList.length - 1];

    let query = this.props.location.search;
    let collection_id = this.getUrlVars(query)["collection_id"];
    let collection_word_id = this.getUrlVars(query)["collection_word_id"];
    this.state.collection_id = collection_id;
    this.state.collection_word_id = collection_word_id;
    this.state.type = type;

    const pagination = {
      current: this.props.redux.pagination.current,
      pageSize: this.props.redux.pagination.pageSize,
      showSizeChanger: true,
      onShowSizeChange: this.onPaginationSizeChange,
      onChange: this.onPaginationChange,
      showTotal: (total, range) => `${range[0]}-${range[1]} of ${total} items`
    };
    const { filterResult } = this.props.redux;
    this.state.pagination = pagination;
    this.state.filterResult = filterResult;
    this.updateTable();
    this.fetchCollectionData(collection_id);
  };

  fetchCollectionData = collection_id => {
    get("/api/english/collection/" + collection_id).then(res => {
      this.setState({
        collectionData: res.data[0]
      });
      log(21, res.data[0]);
    });
  };
  onPaginationSizeChange = (current, size) => {
    let { pagination, filterResult } = this.state;
    pagination.pageSize = size;
    this.state.pagination = pagination;
    this.props.setValue({ pagination });

    this.updateTable();
  };
  onPaginationChange = (page, pageSize) => {
    let { pagination, filterResult } = this.state;
    pagination = { ...pagination, current: page, pageSize };
    this.state.pagination = pagination;
    this.props.setValue({ pagination });

    this.updateTable();
  };
  filterResult = values => {
    let { pagination, filterResult } = this.state;
    pagination.current = 1;
    filterResult = values;
    Object.keys(filterResult).forEach(item => {
      if (!filterResult[item]) {
        filterResult[item] = "";
      }
    });
    this.state.filterResult = filterResult;
    this.props.setValue({ pagination, filterResult });
    this.state.pagination = pagination;
    this.updateTable();
  };
  updateTable = () => {
    const {
      pagination,
      filterResult,
      collection_id,
      type,
      collection_word_id
    } = this.state;
    if (!this.Interceptor()) {
      return;
    }
    this.setState({
      searchButtonLoading: true
    });
    let params = {
      pageNo: pagination.current,
      pageSize: pagination.pageSize,
      ...filterResult
    };
    // params = Object.assign({}, params, values);
    get(routePath, params)
      .then(res => {
        pagination.total = res.data.count;
        const source = (res.data || []).map((item, index) => {
          const temp = {
            key: item.id,
            index: (pagination.current - 1) * pagination.pageSize + index + 1,
            collection_id,
            collection_word_id,
            simple_id: item.simple_id,
            type,
            ...item
          };
          return temp;
        });
        this.setState({
          searchButtonLoading: false,
          source,
          pagination
        });
        message.success("查询成功");
      })
      .catch(err => {
        this.setState({
          searchButtonLoading: false
        });
        message.warn(err.errMsg);
      });
  };
  Interceptor = () => {
    return true;
  };

  createItem = () => {
    this.props.history.push(routePagePath + "/create");
  };

  goToLast = () => {
    this.props.history.go(-1);
  };
  render() {
    const {
      searchButtonLoading,
      source,
      pagination,
      filterResult,
      collectionData
    } = this.state;
    const array = { filters, searchButtonLoading, values: filterResult };
    const tableData = { columns, source, pagination };
    return (
      <div className="simple-container">
        <Filter
          array={array}
          filterResult={this.filterResult}
          createItem={this.createItem}
        />
        <div className="header">
          <span className="span">{`词库：${collectionData.name}  ${
            collectionData.note
          } `}</span>
          <Button type="primary" onClick={this.goToLast} className="btn-back">
            返回
          </Button>
        </div>
        <Table data={tableData} />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  redux: state.simpleWord
});
const mapDispatchToProps = dispatch => {
  return {
    setValue: value => {
      dispatch(setValue(value));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(index);
